import React from 'react'
import { useNavigate} from 'react-router-dom'
import './Home.css'

function Home() {

    const naviage = useNavigate()
    

    return (
        <div className='home-rebdls'>
            <div className='pages-links-container'>
                <div className='page-link' onClick={() => naviage('/pages')} >Pages</div>
                <div className='page-link' onClick={() => naviage('/urls')} >URLS</div>
                <div className='page-link' onClick={() => naviage('/logs')} >Logs</div>
            </div>
        </div>
    )

}

export default Home